.video-container {
  position: fixed;
  width: 100%;
  padding-top: 56.25%; /* 16:9 aspect ratio */
}

.shaka-video {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*css marque */
.marquee-text {
  font-size: 20px;
  color: #d98d18;
  margin-top: 25px;
  padding: 10px;
  text-align: center;
  width: 100%;
}

/* Kontrol responsif */
@media screen and (max-width: 950px) {
  .shaka-video::-webkit-media-controls-timeline,
  .shaka-video::-webkit-media-controls-current-time-display,
  .shaka-video::-webkit-media-controls-time-remaining-display {
    display: none !important;
  }

  .shaka-video::-webkit-media-controls-volume-slider {
    bottom: 0;
    display: block;
  }
}

/* Kontrol untuk layar kecil */
@media screen and (max-width: 480px) {
  .shaka-video::-webkit-media-controls-play-button {
    display: none !important;
  }

  .shaka-video::-webkit-media-controls-mute-button {
    display: none !important;
  }
}

/* Kontrol untuk layar penuh */
.shaka-video.fullscreen::-webkit-media-controls-play-button {
  display: none !important;
}

.shaka-video.fullscreen::-webkit-media-controls-mute-button {
  display: none !important;
}
