/* style.css */
body {
  background-color: #1c2b2d;
  color: #d98d18;
}

.Home {
  text-align: center;
  margin: 0 auto;
  max-width: 800px;
  padding: 20px;
}

header {
  background-color: #d98d18;
  color: white;
  padding: 5px;
  
}

header h1 {
  font-size: 24px;
}

.marquee-text {
  font-size: 20px;
  color: white;
  padding: 10px;
  text-align: center;
  width: 100%;
}

main {
  margin-top: 20px;
}

.channel-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  justify-content: center;
}

.channel-card {
  border: 3px solid #d98d18;
  border-radius: 10px;
  padding: 20px;
  transition: transform 0.2s, box-shadow 0.2s;
  cursor: pointer;
}

.channel-card:hover {
  transform: scale(1.05);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5), 0 0 20px rgba(255, 255, 255, 0.5);
}

.channel-card:active {
  transform: scale(0.95);
  box-shadow: none;
}

.channel-card img {
  width: 100px;
  height: auto;
  margin-bottom: 10px;
}

footer {
  margin-top: 10px;
  background-color: #d98d18;
  color: white;
  padding: 5px;
  position: relative;
}
